  }
}

.mml__icon {
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: #f06247cc;
  border: 1px solid #fff3;
  border-radius: 3rem;
  padding: 1.6rem 1.8rem;
  transition: all .3s;
  display: inline-block;
  position: absolute;
}

.mml__icon svg {
  width: 8rem;
  height: auto;
  color: #fff;
}

.mml__icon svg path {
  stroke-width: 1px;
}

.mml__icon--top {
  top: 30px;
  left: 35%;
  transform: rotate(-14deg);
}

.mml__icon--bottom {
  bottom: 30px;
  right: 35%;
  transform: rotate(-8deg);
}

.mml__icon--right {
  top: 80px;
  right: 7%;
  transform: rotate(15deg);
}

.mml__icon--right svg {
  width: 10rem;
}

.mml__icon--left {
  bottom: 80px;
  left: 5%;
  transform: rotate(15deg);
}

.mml__icon--left svg {
  width: 10rem;
}

@media (width >= 670px) {
  .mml:hover .mml__icon {
    background-color: #f06247;
  }

  .mml:hover .mml__icon--top {
    top: 0;
    left: -3%;
  }

  .mml:hover .mml__icon--bottom {
    bottom: -20px;
    right: -5%;
  }

  .mml:hover .mml__icon--right {
