  }

  .mml__icon--bottom {
    bottom: -20px;
    right: -5%;
    transform: rotate(-5deg);
  }

  .mml__icon--right {
    top: 0;
    right: -7%;
    transform: rotate(5deg);
  }

  .mml__icon--left {
